}
.k-reorder-cue::before,
.k-reorder-cue::after {
  content: "";
  width: 0;
  height: 0;
  border: 3px solid transparent;
  position: absolute;
  transform: translateX(-50%);
}
.k-reorder-cue::before {
  border-bottom-width: 0;
