  background-color: transparent;
  /*remove gray background*/
}
.k-grid-header th.k-header > .k-link {
  display: block;
  min-height: 18px;
  line-height: 18px;
  /* due to sorting icons*/
  margin: -0.5em -0.6em -0.4em 0;
