  text-align: left;
  padding: .1em .2em;
}
.k-chart-shared-tooltip-marker {
  display: block;
  width: 15px;
  height: 3px;
}
/*Stock Charts*/
/* Selection */
.k-selector {
  position: absolute;
  -webkit-transform: translateZ(0);
}
.k-selection {
  position: absolute;
  border-width: 1px;
  border-style: solid;
  border-color: #d2d2d2;
  border-bottom: 0;
  height: 100%;
}
.k-selection-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
  background-color: rgba(255, 255, 255, 0.01);
  filter: alpha(opacity=1);
}
.k-handle {
  background: #d2d2d2;
  width: 7px;
  height: 26px;
  cursor: e-resize;
  z-index: 1;
  border-radius: 6px;
  position: absolute;
}
.k-handle div {
  width: 100%;
  height: 100%;
  background-color: transparent;
}
.k-left-handle {
  left: -4px;
}
.k-right-handle {
  right: -4px;
}
.k-left-handle div {
  margin: -20px 0 0 -15px;
  padding: 40px 30px 0 0;
}
.k-left-handle.k-handle-active div {
  margin-left: -40px;
  padding-right: 55px;
}
.k-right-handle div {
  margin: -20px 0 0 -15px;
  padding: 40px 0 0 30px;
}
.k-right-handle.k-handle-active div {
  padding-left: 55px;
}
.k-mask {
  position: absolute;
  height: 100%;
  background-color: #fff;
  filter: alpha(opacity=80);
  opacity: 0.80;
}
.k-border {
  background: #d2d2d2;
  width: 1px;
  height: 100%;
  position: absolute;
}
/* Navigator hint */
.k-navigator-hint div {
  position: absolute;
}
.k-navigator-hint .k-scroll {
  position: absolute;
  height: 4px;
  border-radius: 4px;
  background: #d2d2d2;
}
.k-navigator-hint .k-tooltip {
  margin-top: 20px;
