  left: 50%;
  margin-left: -9px;
}
div.k-navigator .k-navigator-e,
div.k-navigator .k-navigator-right {
  right: 2px;
  top: 50%;
  margin-top: -9px;
}
div.k-navigator .k-navigator-s,
